$primary-color: #007bff;
$background-color: #f4f4f9;
$border-color: rgba(0, 0, 0, 0.1);

.profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: $background-color;
}

.profile-card {
  background: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 10px $border-color;
  text-align: center;
  width: 350px;

  img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 20px;
  }

  h2 {
    color: $primary-color;
    margin-bottom: 10px;
  }

  p {
    color: #666;
    font-size: 16px;
  }
}
